<template>
	<view>
		<view class="top-box">
			<image class="avatar" :src="userinfo.avatarUrl"></image>
			<text class="nickname">{{userinfo.nickName}}</text>
		</view>
		<button @click="logout">退出登录</button>
	</view>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	export default {
		name: "my-userinfo",
		data() {
			return {
				
			};
		},
		methods: {
			async logout() {
				// 询问用户是否退出登录
				const [err, succ] = await uni.showModal({
					title: '提示',
					content: '确认退出登录吗？'
				}).catch(err => err)
				if (succ && succ.confirm) {
					// 用户确认了退出登录的操作
					// 需要清空 vuex 中的 userinfo、token
					this.updateUserInfo({})
					this.updateToken('')
				}
			}
		},
		computed: {
			...mapMutations('m_user', ['updateUserInfo', 'updateToken']),
			...mapState('m_user', ['userinfo'], ['token']),
		},
	}
</script>

<style lang="scss">
	.top_box{
		
	}
	.avatar {
	}

	.nickname {
	}
</style>
